@import url(https://fonts.googleapis.com/css2?family=Londrina+Solid:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@200;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  font-size: 62.5%;
  /* font-family: "DM Sans", sans-serif; */
  /* scroll-behavior: smooth; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logo{
    font-family: 'Londrina Solid', cursive;
    font-size: 3.4rem;
    padding: 10px;
}

.logo sup {
    font-style: oblique;
    font-size: 1.4rem;
    transition: all 0.2s ease-out;
}

sup:hover {
    color: rgb(7, 0, 68);
}

header{
    width: 100%;
    /* border: 1px solid red; */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
li{
    list-style: none;
}

.container-general{
    width: 80%;
    margin: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-top: 25px;
}


.item{
    min-width: 30%;
    background: #f3f3f3;
    box-shadow: 0px 3px 4px 0px rgb(0 0 0 / 30%);
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.item h2{
    font-size: 2.4rem;
}

.item p{
    font-size: 1.4rem;
    margin: auto;
}

.title-h3{
    font-family: 'Inconsolata', monospace;
    text-align: center;
    font-size: 2.4rem;
}
.container-buttons{
    width: 80%;
    margin: auto;
    margin-top: 3.4rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.card {
  width: 40%;
  height: 50px;
  border: 1px solid #000;
  margin-bottom: 20px;
  /*padding: 10px;
  */font-size: 1.4rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-transform: uppercase;
  box-shadow: 8px 8px 0px 0px #000000;
  transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  cursor: pointer;
}
.card:hover{
    box-shadow: 15px 15px 0px 0px #000000;
}

.card p {
  margin: 0px;
}

.title-card {
  font-size: 1.8rem;
}

.link{
  color: #000;
  text-decoration: none;
}

